<template>
<v-container>
  <!-- Tab bar -->
  <v-row class="ma-0 pa-0">
    <v-col class="ma-0 pa-0">
      <template>
        <v-tabs v-model="tab" id="tabBar">
          <v-tabs-slider ></v-tabs-slider>
          <v-tab v-for="item in tabItems" :key="item.tab" @click="tabChange(item.tab)">
            {{ item.tab }}
          </v-tab>
        </v-tabs>
      </template>
    </v-col> 
  </v-row>
  <!-- Tab bar -->
  <!-- Data Table -->
  <v-row class="ma-0 pa-0">
    <v-col class="ma-0 pa-0">
      <v-data-table :headers="headers" :items="stores" item-key="id" class="elevation-1" disable-pagination hide-default-footer multi-sort fixed-header height="100%" id="dataTableContent">
        <template v-slot:item="props">
          <tr @click="onStoreSelect(props.item)" style="cursor:pointer">
            <td align="center">
              {{ props.item.platform }}
            </td>
            <td align="center">
              {{ props.item.name }}
            </td>
            <td align="start">
              {{ props.item.url }}
            </td>
            <td align="center">
              <v-img v-if="props.item.isIntegration" alt="" class="shrink mt-1 hidden-sm-and-down" contain src="@/images/checkOn.png" width="25px" />
              <v-img v-else alt="" class="shrink mt-1 hidden-sm-and-down" contain src="@/images/checkOff.png" width="25px" />
            </td>
          </tr>
        </template>
      </v-data-table>
    </v-col>
  </v-row>
  <v-row class="ma-0 pa-0">
    <v-col class="ma-0 pa-0" align="right">
      <v-tooltip top color="#E3B365">
        <template v-slot:activator="{ on, attrs }">
          <v-img v-on="on" v-bind="attrs" style="cursor:pointer;top:5px" contain src="@/images/add.png" width="43px" @click="onStoreAdd" />
        </template>
        <span>New</span>
      </v-tooltip>      
    </v-col>
  </v-row>
  <!-- Data Table -->
  <v-row>
    <v-col align="start">
      <v-btn style="background-color: #FFC971;">Information</v-btn>
    </v-col>
  </v-row>
  <v-row class="mt-0">
    <v-col>
      <v-text-field v-model="sellerData.firstName" dense hide-details label="First Name" placeholder="First Name" outlined ref="firstName"></v-text-field>
    </v-col>
    <v-col>
      <v-text-field v-model="sellerData.lastName" dense hide-details label="Last Name" placeholder="Last Name" outlined ref="lastName"></v-text-field>
    </v-col>
    <v-col>
      <v-text-field v-model="sellerData.email" dense hide-details label="Email Address" placeholder="Email Address" outlined ref="email"></v-text-field>
    </v-col>
    <v-col>
      <v-text-field v-model="sellerData.phoneNo" dense hide-details label="Mobile Number" placeholder="Mobile Number" outlined ref="mobileNumber" oninput="javascript: this.value = this.value.replace(/\D/g, '' );"></v-text-field>
    </v-col>
  </v-row>
  <v-row class="mt-0">
    <v-col style="display: flex">
      <vue-country-code @onSelect="onCountrySelect" v-bind:defaultCountry="defaultCountry"></vue-country-code>
        &nbsp;
        <v-text-field v-model="sellerData.addressCountry" dense hide-details label="Country (*)" placeholder="ex) KR / US / JP / CN" outlined ref="addressCountry" disabled filled></v-text-field>
    </v-col>
    <v-col>
      <v-text-field v-model="sellerData.zipCode" dense hide-details label="Post Code" placeholder="Post Code" outlined ref="zipCode"></v-text-field>
    </v-col>    
    <v-col>
      <v-text-field v-model="sellerData.address1" dense hide-details label="Address 1" placeholder="Address 1" outlined ref="address1"></v-text-field>
    </v-col>
    <v-col>
      <v-text-field v-model="sellerData.address2" dense hide-details label="Address 2" placeholder="Address 2" outlined ref="address2"></v-text-field>
    </v-col>
    <v-col>
      <v-text-field v-model="sellerData.addressCity" dense hide-details label="City" placeholder="City" outlined ref="addressCity"></v-text-field>
    </v-col>
  </v-row>
  <v-row class="mt-0">
    <v-col>
      <v-text-field v-model="sellerData.addressState" dense hide-details label="State" placeholder="State" outlined ref="addressState"></v-text-field>
    </v-col>    
    <v-col>
      <v-text-field v-model="sellerData.companyName" dense hide-details label="Company Name" placeholder="Company Name" outlined ref="companyName"></v-text-field>
    </v-col>
    <v-col>
      <v-text-field v-model="sellerData.companyTelNo" dense hide-details label="Company Tel No." placeholder="Company Tel No." outlined ref="companyTelNo"></v-text-field>
    </v-col>
    <v-col>
      <v-text-field v-model="sellerData.companyRegisterNo" dense hide-details label="Company Registration No." placeholder="Company Registration No." outlined ref="companyRegisterNo"></v-text-field>
    </v-col>
    <v-col>
      <v-text-field dense hide-details label="URL" placeholder="URL" outlined ref="url"></v-text-field>
    </v-col>
  </v-row>
  <v-row>
    <v-col align="right">
      <v-btn color="primary" @click="OnSellerUpdate">UPDATE</v-btn>
    </v-col>
  </v-row>
  <v-row>
    <v-col align="start">
      <v-btn style="background-color: #FFC971;">Recommender</v-btn><v-btn v-if="this.recommender" style="background-color: #E0E0E0; margin-left: 10px;">{{this.recommender.companyName}}</v-btn>
    </v-col>
  </v-row>
  <v-row>
    <v-col align="start">
      <v-btn style="background-color: #FFC971;">Recommendee</v-btn>
      <v-btn v-for="(recommendee, index) in this.recommendees"  :key="index" style="background-color: #E0E0E0; margin-left: 10px;">{{recommendee.companyName}}</v-btn>
    </v-col>
  </v-row>
    <!-- 로딩바 -->
    <loadingBarView :setLoadingBar="this.setLoadingBar"></loadingBarView>
    <!-- 로딩바 -->   
    <!-- store 등록 다이얼로그 -->
    <v-dialog v-model="storeCreateDialogShow" persistent max-width="500px">
      <template v-slot:activator="{ on, attrs }">
        <v-btn color="primary" dark v-bind="attrs" v-on="on" disabled style="display: none"> Open Dialog </v-btn>
      </template>
      <v-card>
        <v-card-text>
          <v-card-title class="ma-0 pa-0">
              <span class="text-h5" style="margin-top: 20px;margin-bottom: 20px;">Marketpace/Platform Registration</span>
            </v-card-title>
            <v-row>
              <v-col>
                <v-text-field dense hide-details label="Marketpace/Platform (*)" placeholder="Marketpace/Platform" outlined v-model="createStore.platform" ref="platform"></v-text-field>
              </v-col>
            </v-row>
            <v-row>
              <v-col>
                <v-text-field dense hide-details label="Store Name (*)" placeholder="Store Name" outlined v-model="createStore.name" ref="storeName"></v-text-field>
              </v-col>
            </v-row>
            <v-row>
              <v-col>
                <v-text-field dense hide-details label="URL (*)" placeholder="http://gemplatforms.com" outlined v-model="createStore.url" ref="url"></v-text-field>
              </v-col>
            </v-row>
            <v-row>
              <v-col>
                <div style="width:190px;"><v-switch v-model="createStore.isIntegration" inset :label="`Integration Status`"></v-switch></div>
              </v-col>
            </v-row>
        </v-card-text>            
        <v-card-actions>
          <v-spacer></v-spacer>
          <v-btn color="error" outlined @click="OnStoreCancel"> Cancel </v-btn>
          <v-btn color="primary" @click="OnStoreCreateOk"> Ok </v-btn>
        </v-card-actions>
      </v-card>
    </v-dialog>
    <!-- store 등록 다이얼로그 -->

    <!-- store 수정 다이얼로그 -->
    <v-dialog v-model="storeModifyDialogShow" persistent max-width="500px">
      <template v-slot:activator="{ on, attrs }">
        <v-btn color="primary" dark v-bind="attrs" v-on="on" disabled style="display: none"> Open Dialog </v-btn>
      </template>
      <v-card>
        <v-card-text>
          <v-card-title class="ma-0 pa-0">
              <span class="text-h5" style="margin-top: 20px;margin-bottom: 20px;">Marketpace/Platform Registration</span>
            </v-card-title>
            <v-row>
              <v-col>
                <v-text-field dense hide-details label="Marketpace/Platform (*)" placeholder="Marketpace/Platform" outlined v-model="modifyStore.platform" ref="platform"></v-text-field>
              </v-col>
            </v-row>
            <v-row>
              <v-col>
                <v-text-field dense hide-details label="Store Name (*)" placeholder="Store Name" outlined v-model="modifyStore.name" ref="storeName"></v-text-field>
              </v-col>
            </v-row>
            <v-row>
              <v-col>
                <v-text-field dense hide-details label="URL (*)" placeholder="http://gemplatforms.com" outlined v-model="modifyStore.url" ref="url"></v-text-field>
              </v-col>
            </v-row>
            <v-row>
              <v-col>
                <div style="width:190px;"><v-switch v-model="modifyStore.isIntegration" inset :label="`Integration Status`"></v-switch></div>
              </v-col>
            </v-row>
        </v-card-text>            
        <v-card-actions>
          <v-spacer></v-spacer>
          <v-btn color="error" outlined @click="OnStoreCancel"> Cancel </v-btn>
          <v-btn color="error" @click="OnStoreDelete"> Delete </v-btn>
          <v-btn color="primary" @click="OnStoreModifyOk"> Ok </v-btn>
        </v-card-actions>
      </v-card>
    </v-dialog>
    <!-- store 수정 다이얼로그 -->
  </v-container>
</template>
<style scoped>
</style>
<script>
import common from '@/common'
import axios from "axios";
import $ from "jquery";
import loadingBarView from "@/components/LoadingBarView.vue"

export default {
    data() {
        return {
            storeCreateDialogShow: false,
            storeModifyDialogShow: false,
            setLoadingBar : { loadingBarDialogShow : false },

            createStore: {},
            modifyStore: {},
            selectedStore: {},
            stores: [],

            sellerData: {},
            recommender: '',
            recommendees: [],
            defaultCountry:'KR',
            
            tab: 0,
            tabItems: [
                { tab: 'My Details', content: 'Tab 1 Content' },
                { tab: 'Payment', content: 'Tab 2 Content' },
                { tab: 'Shipping', content: 'Tab 3 Content' },
                { tab: 'Packaging', content: 'Tab 4 Content' },
            ],
            headers: [
                { text: "Marketplace/Platform", value: "platform", width: "250", align: "center" },
                { text: "Store Name", value: "storeName", width: "250", align: "center" },
                { text: "URL", value: "url", width: "1000" },
                { text: "Integration Status", value: "integrationStatus", width: "", align: "center" },
            ],
        };
    },
    components: {
      loadingBarView
    },
    filters: {       
    },
    computed: {
    },
    methods: {
        onStoreSelect(store) {
            this.selectedStore = store;
            this.modifyStore = {...store};
            this.storeModifyDialogShow = true;
        },
        onStoreAdd() {
            this.createStore = {};
            this.storeCreateDialogShow = true;
        },
        tabChange(value) {
            //if(value == "My Details") { this.$router.push("/seller-master"); }
            if(value == "Payment") { this.$router.push("/seller-master-payment"); }
            if(value == "Shipping") { this.$router.push("/seller-master-shipping"); }
            if(value == "Packaging") {this.$router.push("/seller-master-packaging"); }
        },
        onCountrySelect({ iso2 }) {
            this.sellerData.addressCountry = iso2;
        },
        getSeller() {
            if (this.$session.get("loginId") == "" || this.$session.get("authorization") == "") {
                this.$alert("Login Fail.", "", "info").then(() => {
                    this.$router.push("/");
                });
                return false;
            }   

            this.setLoadingBar.loadingBarDialogShow = true;

            axios.get(common.makeAPIUrl('/sellers/' + this.$session.get('loginId'))
                    , { headers: {'Authorization': this.$session.get('authorization')}}
            )
            .then(response => {
                this.sellerData = response.data;
                this.defaultCountry = this.sellerData.addressCountry;
                this.setLoadingBar.loadingBarDialogShow = false;
            })
            .catch(err => {
                if(err.response.status == 401) {
                    this.$alert("Login Fail.", "", "info").then(() => {
                        this.$router.push("/");
                    });
                    return false;
                }
                this.$alert(err.response.data.message, '', 'info').then(() => {  })
                this.setLoadingBar.loadingBarDialogShow = false;
            })
        },
        getStores() {
            if (this.$session.get("loginId") == "" || this.$session.get("authorization") == "") {
                this.$alert("Login Fail.", "", "info").then(() => {
                    this.$router.push("/");
                });
                return false;
            }      

            this.setLoadingBar.loadingBarDialogShow = true;

            axios.get(common.makeAPIUrl('/sellers/' + this.$session.get('loginId') + '/stores')
                    , { headers: {'Authorization': this.$session.get('authorization')}}
            )
            .then(response => {
                this.stores = response.data;
                this.setLoadingBar.loadingBarDialogShow = false;
            })
            .catch(err => {
                if(err.response.status == 401) {
                    this.$alert("Login Fail.", "", "info").then(() => {
                        this.$router.push("/");
                    });
                    return false;
                }
                this.$alert(err.response.data.message, '', 'info').then(() => {  })
                this.setLoadingBar.loadingBarDialogShow = false;
            })
        },
        getRecommender() {
            if (this.$session.get("loginId") == "" || this.$session.get("authorization") == "") {
                this.$alert("Login Fail.", "", "info").then(() => {
                    this.$router.push("/");
                });
                return false;
            }      

            this.setLoadingBar.loadingBarDialogShow = true;

            axios.get(common.makeAPIUrl('/sellers/' + this.$session.get('loginId') + '/recommender')
                    , { headers: {'Authorization': this.$session.get('authorization')}}
            )
            .then(response => {
                this.recommender = response.data;
                this.setLoadingBar.loadingBarDialogShow = false;
            })
            .catch(err => {
                if(err.response.status == 401) {
                    this.$alert("Login Fail.", "", "info").then(() => {
                        this.$router.push("/");
                    });
                    return false;
                }
                this.$alert(err.response.data.message, '', 'info').then(() => {  })
                this.setLoadingBar.loadingBarDialogShow = false;
            })
        },
        getRecommendees() {
            if (this.$session.get("loginId") == "" || this.$session.get("authorization") == "") {
                this.$alert("Login Fail.", "", "info").then(() => {
                    this.$router.push("/");
                });
                return false;
            }

            this.setLoadingBar.loadingBarDialogShow = true;

            axios.get(common.makeAPIUrl('/sellers/' + this.$session.get('loginId') + '/recommendee')
                    , { headers: {'Authorization': this.$session.get('authorization')}}
            )
            .then(response => {
                this.recommendees = response.data;
                this.setLoadingBar.loadingBarDialogShow = false;
            })
            .catch(err => {
                if(err.response.status == 401) {
                    this.$alert("Login Fail.", "", "info").then(() => {
                        this.$router.push("/");
                    });
                    return false;
                }
                this.$alert(err.response.data.message, '', 'info').then(() => {  })
                this.setLoadingBar.loadingBarDialogShow = false;
            })
        },
        OnStoreCancel() {
            this.storeCreateDialogShow = false;
            this.storeModifyDialogShow = false;

            this.createStore = {};
            this.modifyStore = {};
        },
        OnStoreDelete() {
            if (this.$session.get("loginId") == "" || this.$session.get("authorization") == "") {
                this.$alert("Login Fail.", "", "info").then(() => {
                    this.$router.push("/");
                });
                return false;
            }       

            this.setLoadingBar.loadingBarDialogShow = true;

            axios.delete(common.makeAPIUrl('/sellers/' + this.$session.get('loginId') + '/stores/' + this.modifyStore.id),
                        { headers: {'Authorization': this.$session.get('authorization')}}
            )
            .then(() => {
                this.setLoadingBar.loadingBarDialogShow = false;
                this.storeModifyDialogShow = false;

                this.modifyStore = {};
                this.selectedStore = {};

                this.getStores();
            })
            .catch(err => {
                if(err.response.status == 401) {
                    this.$alert("Login Fail.", "", "info").then(() => {
                        this.$router.push("/");
                    });
                    return false;
                }
                this.setLoadingBar.loadingBarDialogShow = false;    
                this.$alert(err.response.data.message, '', 'info').then(() => {  })
            })
        },
        OnStoreCreateOk() {
            if (this.$session.get("loginId") == "" || this.$session.get("authorization") == "") {
                this.$alert("Login Fail.", "", "info").then(() => {
                    this.$router.push("/");
                });
                return false;
            }       
           
            this.setLoadingBar.loadingBarDialogShow = true;
            axios.post(common.makeAPIUrl('/sellers/' + this.$session.get('loginId') + '/stores'),
                        {
                            platform: this.createStore.platform,
                            name: this.createStore.name,
                            url: this.createStore.url,
                            isIntegration: (this.createStore.isIntegration == null || this.createStore.isIntegration == '') ? false : this.createStore.isIntegration,
                            sellerId: this.$session.get('loginId')
                        },
                        { headers: {'Authorization': this.$session.get('authorization')}}
            )
            .then(() => {
                    this.setLoadingBar.loadingBarDialogShow = false;
                    this.storeCreateDialogShow = false;
                    this.createStore = {};
                    this.getStores();
            })
            .catch(err => {
                if(err.response.status == 401) {
                    this.$alert("Login Fail.", "", "info").then(() => {
                        this.$router.push("/");
                    });
                    return false;
                }
                this.setLoadingBar.loadingBarDialogShow = false;    
                this.$alert(err.response.data.message, '', 'info').then(() => {  })
                
            })
        },
        OnStoreModifyOk() {
            if (this.$session.get("loginId") == "" || this.$session.get("authorization") == "") {
                this.$alert("Login Fail.", "", "info").then(() => {
                    this.$router.push("/");
                });
                return false;
            }
           
            this.setLoadingBar.loadingBarDialogShow = true;
            axios.put(common.makeAPIUrl('/sellers/' + this.$session.get('loginId') + '/stores'),
                    {
                        id: this.modifyStore.id,
                        platform: this.modifyStore.platform,
                        name: this.modifyStore.name,
                        url: this.modifyStore.url,
                        isIntegration: (this.modifyStore.isIntegration == null || this.modifyStore.isIntegration == '') ? false : this.modifyStore.isIntegration,
                        sellerId: this.$session.get('loginId')
                    },
                    { headers: {'Authorization': this.$session.get('authorization')}}
            )
            .then(() => {
                this.setLoadingBar.loadingBarDialogShow = false;
                this.storeModifyDialogShow = false;
                this.modifyStore = {};
                this.getStores();
            })
            .catch(err => {
                if(err.response.status == 401) {
                    this.$alert("Login Fail.", "", "info").then(() => {
                        this.$router.push("/");
                    });
                    return false;
                }
                this.setLoadingBar.loadingBarDialogShow = false;    
                this.$alert(err.response.data.message, '', 'info').then(() => {  })
            })
        },
        OnSellerUpdate() {
            if (this.$session.get("loginId") == "" || this.$session.get("authorization") == "") {
                this.$alert("Login Fail.", "", "info").then(() => {
                    this.$router.push("/");
                });
                return false;
            }
            //셀러 업데이트 유효성 체크
            if (!/^[a-zA-Z0-9.!#$%&'*+/=?^_`{|}~-]+@[a-zA-Z0-9-]+(?:\.[a-zA-Z0-9-]+)*$/.test(this.sellerData.email)) {
              this.$alert(common.messages.email, "", "info").then(() => {
                this.$refs.email.focus();
              });
              return false;
            }
            if (!this.sellerData.firstName) {
              this.$alert(common.messages.firstName, "", "info").then(() => {
                this.$refs.firstName.focus();
              });
              return false;
            }
            if (!this.sellerData.lastName) {
              this.$alert(common.messages.lastName, "", "info").then(() => {
                this.$refs.lastName.focus();
              });
              return false;
            }
            if (!this.sellerData.phoneNo) {
              this.$alert(common.messages.mobileNo, "", "info").then(() => {
                this.$refs.mobileNumber.focus();
              });
              return false;
            }
            if (!this.sellerData.addressCountry) {
              this.$alert(common.messages.addressCountry, "", "info").then(() => {
                this.$refs.addressCountry.focus();
              });
              return false;
            }
            if (!this.sellerData.zipCode) {
              this.$alert(common.messages.zipCode, "", "info").then(() => {
                this.$refs.zipCode.focus();
              });
              return false;
            }
            if (!this.sellerData.address1) {
              this.$alert(common.messages.address1, "", "info").then(() => {
                this.$refs.address1.focus();
              });
              return false;
            }
            if (!this.sellerData.addressCity) {
              this.$alert(common.messages.addressCity, "", "info").then(() => {
                this.$refs.addressCity.focus();
              });
              return false;
            }
            if (!this.sellerData.addressState) {
              this.$alert(common.messages.addressState, "", "info").then(() => {
                this.$refs.addressState.focus();
              });
              return false;
            }
            if (!this.sellerData.companyName) {
              this.$alert(common.messages.companyName, "", "info").then(() => {
                this.$refs.companyName.focus();
              });
              return false;
            }
            if (!this.sellerData.companyTelNo) {
              this.$alert(common.messages.companyTelNo, "", "info").then(() => {
                this.$refs.companyTelNo.focus();
              });
              return false;
            }
            if (!this.sellerData.companyRegisterNo) {
              this.$alert(common.messages.companyRegisterNo, "", "info").then(() => {
                this.$refs.companyRegisterNo.focus();
              });
              return false;
            }
            this.setLoadingBar.loadingBarDialogShow = true;
            axios.put(common.makeAPIUrl('/sellers/' + this.$session.get('loginId')),
                    this.sellerData,
                    { headers: {'Authorization': this.$session.get('authorization')}}
            )
            .then(() => {
                this.setLoadingBar.loadingBarDialogShow = false;
                this.storeModifyDialogShow = false;
                this.modifyStore = {};
                this.getStores();
            })
            .catch(err => {
                if(err.response.status == 401) {
                    this.$alert("Login Fail.", "", "info").then(() => {
                        this.$router.push("/");
                    });
                    return false;
                }
                this.setLoadingBar.loadingBarDialogShow = false;    
                this.$alert(err.response.data.message, '', 'info').then(() => {  })
            })
        }
    },
    mounted() {
        if (this.$session.get("loginId") == "" || this.$session.get("authorization") == "") {
          this.$alert(common.messages.login, "", "info").then(() => {
            this.$router.push("/");
          });
          return false;
        }
              
        $(document).prop("title", "Seller Master");
        $("#bar_header").attr("style", "background-color:#E2E1F1");
        $("#top_header").show();
        $("#topNaviText").text("Seller Master");        

        var dataTableHeight = "height:" + String(window.innerHeight - 650) + "px";
        $("#dataTableContent").children().attr("style", dataTableHeight);  
        
        $("#tabBar>div>div>div>div").next().css({'background-color':'#6587CD' });
        $("#tabBar>div>div>div>div").next().next().css({'background-color':'#949494' });
        $("#tabBar>div>div>div>div").next().next().next().css({'background-color':'#949494' });
        $("#tabBar>div>div>div>div").next().next().next().next().css({'background-color':'#949494' });
        $("#tabBar>div>div>div>div").css({'margin-right':'8px' });
        $("#tabBar>div>div>div>div").css({'color':'#FFFFFF' });
        
        this.getSeller();
        this.getStores();
        this.getRecommender();
        this.getRecommendees();
    },
    updated() {
        $(".v-data-table-header>tr>th").css({'background-color':'#FFC971' });
        $(".v-data-table-header>tr>th").css({'text-align':'center' });
        $(".v-data-table-header>tr>th").css({'font-size':'15px' });
        $(".v-data-table-header>tr>th").css({'font-style':'bold' });
    },
};
</script>